@import "./../../global";

.about {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 3rem;
  margin: 4rem 0;
  .img-wrapper {
    // padding: 2rem;
    img {
      height: 400px;
      width: 100%;
      object-fit: cover;
      border-radius: 1rem;
      position: relative;
    }
  }
  .text-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    .text-content {
      display: flex;
      flex-direction: column;
      h2 {
        color: darken($primary, 20);
      }
      p {
        color: $text-dark;
        line-height: 1.5rem;
        margin: 1.5rem 0;
      }
      .about-btn {
        display: flex;
      }
    }
  }
}

// responsive

@media only screen and (max-width: 768px) {
  .about {
    grid-template-columns: auto;
    grid-gap: 1.5rem;
    margin: 2rem 0;
    .img-wrapper {
      img {
        height: 320px;
      }
    }
    .text-wrapper {
      .text-content {
        .about-btn {
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .about {
    .img-wrapper {
      img {
        height: 240px;
      }
    }
    .text-wrapper {
      .about-btn {
        button {
          width: 100%;
        }
      }
    }
  }
}
